@mixin prefix($property, $value, $vendors: webkit moz ms o) {
  @if $vendors {
    @each $vendor in $vendors {
      #{"-" + $vendor + "-" + $property}: #{$value}; } }
  #{$property}: #{$value}; }


@mixin keyframes( $animationName ) {
  @-webkit-keyframes #{$animationName} {
    @content; }

  @-moz-keyframes #{$animationName} {
    @content; }

  @-o-keyframes #{$animationName} {
    @content; }

  @keyframes #{$animationName} {
    @content; } }

@mixin animation($name, $speed, $form, $iteration: 1) {
  @include prefix(animation, $name $speed $form $iteration); }

@mixin filter($name) {
  @include prefix(filter, $name); }

@mixin text-shadow($offset-x, $offset-y, $blur-radius, $color) {
  @include prefix(text-shadow, $offset-x $offset-y $blur-radius $color); }

@mixin user-select($value) {
  @include prefix(user-select, $value); }

@mixin box-shadow($horizontalLength, $verticalLength, $blurRadius, $spreadRadius, $shadowColor) {
  @include prefix(box-shadow, $horizontalLength $verticalLength $blurRadius $spreadRadius $shadowColor); }

@mixin transition($args...) {
  @include prefix(transition, $args); }
