@import "partials/basic";
@import "partials/variables";
@import "partials/mixin";


.Wisielec {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-direction: column;
	padding: 20px;
	background: $background;

	.letters {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }

	.proverb {
		display: flex;
		justify-content: center;
		margin: 10px;
		letter-spacing: 2px;
		&:hover sup {
			opacity: 1; }
		sup {
			@include transition(all .5s ease);
			opacity: 0;
			font-size: 10px;
			letter-spacing: 0px; } }

	.keyboard {
		width: 450px;
		display: grid;
		grid-template-columns: repeat(6, 16.6%);

		button {
			border-radius: 100%;
			margin: 2px 5px;
			height: 60px;
			width: 60px;
			border: none;
			outline: none;
			cursor: pointer;
			background: $buttons;
			position: relative;
			@include box-shadow(0px, 0px, 4px, 0px, rgba(0,0,0,0.75));
			@include transition(all 0.5s ease);
			&:hover {
				background: $buttonsHover; }
			&:active {
				@include box-shadow(0px, 0px, 15px, 0px, rgba(0,0,0,0.75)); }

			&:nth-child(31) {
				grid-column-start: 3; } } }

	canvas {
		@include box-shadow(0px, 0px, 24px, 4px, rgba(0,0,0,0.75));
		background: $green;
		border-radius: 12px; } }
